.wrapper {
    min-width: 200px;
    display: inline-flex;
    justify-content: space-around;
}

.pre_junior {
    color: orange;
    text-decoration: none;
}

.junior {
    color: blueviolet;
    text-decoration: none;
}

.junior_plus {
    color: forestgreen;
    text-decoration: none;
}

.dropbtn {
    background-color: blue;
    color: white;
    padding: 16px;
    font-size: 10px;
    border: none;
}

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown_content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 200px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.dropdown_content NavLink {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.dropdown-content a:hover {
    background-color: #ddd;
}

.dropdown:hover .dropdown_content {
    display: inline-flex;
    justify-content: space-around;
    background-color: aquamarine
}

.dropdown:hover .dropbtn {
    background-color: #3e8e41;
}